@charset "utf-8";

.kolonsport-site {
  .franchise {
    margin: 78px 0 140px;
    color: #1f1f1f;
    h3 {
      color: #1f1f1f;
      font-size: 22px;
      margin-bottom: 52px;
    }
    h5 {
      clear: both;
      font-size: 18px;
      font-weight: bold;
      margin-top: 65px;
      margin-bottom: 28px;
      padding-bottom: 16px;
      border-bottom: 3px solid #1f1f1f;
    }
    .checkbox {
      margin-right: 12px;
    }
    dl.franchise-intro {
      font-size: 14px;
      dt {
        float: left;
        width: 126px;
        padding-top: 33px;
        border-top: 1px solid #dedede;
        font-weight: bold;
        line-height: 1.5;
        color: #1f1f1f;
      }
      dd {
        float: left;
        width: 846px;
        padding: 33px 0;
        border-top: 1px solid #dedede;
        color: #898989;
        .recommend {
          span {
            line-height: 1.5;
          }
          a.btn {
            float: right;
            width: 160px;
            height: 40px;
            margin-right: 21px;
            padding: 0;
            font-size: 14px;
            line-height: 40px;
            text-align: center;
          }
        }
        .list-dot-style {
          li {
            font-size: 14px;
            color: #898989;
            padding: 0 0 0 8px;
            &::before {
              top: 10px;
              background-color: #898989;
            }
          }
        }
        .step {
          display: inline-block;
          margin: 0 0 50px -18px;
          font-size: 0;
          span {
            position: relative;
            float: left;
            display: inline-block;
            min-width: 86px;
            margin-right: 7px;
            padding-top: 56px;
            font-size: 12px;
            text-align: center;
            color: #666;
            img {
              position: absolute;
              top: 0;
              left: 50%;
              margin-left: -23px;
            }
          }
        }
        .support {
          p {
            margin-top: 25px;
          }
        }
        .ps {
          font-size: 12px;
        }
        strong {
          font-weight: bold;
          color: #1f1f1f;
        }
      }
      table {
        width: 100%;
        border-spacing: 0;
        margin-top: 25px;
        color: #777;
        tr {
          td {
            &.head {
              padding: 0 23px;
              border: 1px solid #dcdcdc;
              span {
                display: inline-block;
                vertical-align: middle;
                line-height: 1.2;
                strong {
                  padding-right: 80px;
                }
              }
            }
            .region {
              display: inline-block;
              width: 92px;
              color: #333;
              &::before {
                content: '· ';
              }
            }
            .tel {
              display: inline-block;
              margin-left: 97px;
              margin-top: 8px;
            }
            .email {
              position: relative;
              padding-left: 37px;
              &::before {
                content: '';
                position: absolute;
                width: 11px;
                height: 8px;
                top: 4px;
                left: 19px;
                background: url(/_ui/img/kolonsport/pc/icn/icn-email.png) no-repeat;
              }
            }
          }
          td {
            width: 50%;
            height: 78px;
            vertical-align: middle;
            border: 1px solid #ededed;
            border-width: 0 1px 1px 0;
          }
          td + td {
            border-right-width: 0;
            padding-left: 24px;
          }
        }
      }
    }
    dl.franchise-form {
      padding-top: 19px;
      font-size: 14px;
      .col-left {
        float: left;
        width: 444px;
        padding-right: 42px;
      }
      .col-right {
        float: left;
        width: 486px;
      }
      dt {
        float: left;
        width: 130px;
        line-height: 31px;
        margin-bottom: 26px;
        .required {
          background-color: #f23d4e;
          border-radius: 50%;
          display: inline-block;
          font: 0/0 a;
          height: 4px;
          margin-left: 5px;
          vertical-align: middle;
          width: 4px;
        }
      }
      dd {
        width: 100%;
        margin-bottom: 26px;
        .col-1 {
          width: 213px;
        }
        .col-2 {
          width: 313px;
        }
        .col-3 {
          width: 821px;
        }
        .col-offset-1 {
          margin-left: 130px;
        }
        .col-33 {
          width: 98px !important;
          margin-right: 6px;
          &:last-child {
            margin-right: 0;
          }
        }
        button,
        select {
          padding: 0;
          width: 97px;
        }
        input,
        select,
        button {
          height: 31px;
          line-height: 31px;
          font-size: 14px;
        }
        select {
          padding-bottom: 4px;
        }
        textarea {
          height: 130px;
          resize: none;
          font-size: 14px;
        }
        input[type="file"] {
          display: none;
        }
        .uploadBtn {
          height: 31px;
          line-height: 31px;
          font-size: 14px;
        }
      }
    }
    .agree_detail {
      background-color: #f5f5f5;
      color: #898989;
      padding: 25px 24px;
      line-height: 1.8;
      font-size: 14px;
      li {
        margin-left: 20px;
        list-style: decimal;
      }
    }
    .buttons {
      border: 0;
      padding: 0;
      margin-top: 47px;
    }
  }
}
